@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
/*! minireset.css v0.0.6 | MIT License | github.com/jgthms/minireset.css */
html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

ul {
  list-style: none;
}

button,
input,
select,
textarea {
  margin: 0;
  background: none;
  border: none;
  padding: 0;
  outline: 0;
}



html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

img,
video {
  height: auto;
  max-width: 100%;
}





td:not([align]),
th:not([align]) {
  text-align: left;
}


/* hide scrollbar */
/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.link {
  text-decoration: none;
}

.header {
    height: 10vh;
    background-color: black;
    justify-content: flex-end;
    padding-right: 3vw;
    padding-bottom: 3px;
    display: flex;
}

.header .nav {
    display: flex;
    align-items: flex-end;

}

.header .toggle {
    margin-bottom: 4px;
}

.header .menu-button {
    height: 40px;
    color: lightgrey;
}

.menu-button:hover {
    cursor: url(data:image/png;base64,), auto; 
}

.header .nav-link {
    color: lightgray;
    font-family: 'VT323', monospace; 
    font-size: 30px;
    margin-right: 3vw;
    padding: 5px;
}

.nav-link:hover {
    color: white;
    /* cursor: url('../assets/cursor1.png'), auto; */
}

@media (max-width: 400px) {
    .header {
        padding-bottom: 7px;
        padding-right: 20px;
    }

    .header .toggle {
        margin-bottom: 12px;
    }

    .header .nav-link {
        font-size: 25px;
    }
}
.game {
    display: flex;
    justify-content: center;
    max-width: 1024px;
    margin: auto;

}

.cell:hover {
    border: 1px solid grey;
    cursor: pointer;
    /* cursor: url('../assets/cursor1.png'), auto; */
}
.grid-wrapper {
    display: flex;
    flex-direction: row;
}

.cols-wrapper {
    display: flex;
    flex-direction: column;
}

.game-menu {
    position: fixed;
    left: 5vw;
    bottom: 5vh;
    display: flex;
}

.game .menu-button {
    color: lightgrey;
    height: 62px;
}

.menu-button img {
    height: 40px;
}

.game .toggle {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.game .intro {
    color: grey;
    font-family: 'VT323', monospace; 
    font-size: 30px;
    padding-left: 1vw;

}

.game .actions {
    padding-left: 1vw;
    display: flex;
}

.actions button,
.actions p {
    color: lightgray;
    font-family: 'VT323', monospace; 
    font-size: 30px;
    margin-right: 2vw;
}

.actions .speed-button {
    padding-left: 4px;
    padding-right: 4px;
    margin-right: 0;
}

.actions #run-button {
    width: 48px;
}


.actions button:hover,
.intro:hover {
    color: white;
    cursor: pointer;
    /* cursor: url('../assets/cursor1.png'), auto; */
    
}

/* mobile */
@media (max-width: 400px) {
    .actions button, .actions p, .game .intro {
        font-size: 20px;
    }

    .actions #run-button {
        width: 32px;
    }

    .menu-button img {
        height: 30px;
    }

}




.about-page {
  font-size: 34px;
}

.about-page .content p {
  margin-bottom: 15px;
}

.about-page .start {
  line-height: 1.2;
}

.about-page .start,
.about-page .setting {
  margin: 5vh 3vw;
  text-align: justify;
  display: flex;
}

.about-page .buttons {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

}

.start .text,
.start .buttons,
.setting .story,
.setting .play {
    padding: 1vh 2vw;
}

.start .text {
  max-width: 800px;
}

.setting .story {
  width: 50vw;
}

.setting .play {
  width: 40vw;
  text-align: center;
}

.setting .continue-button {
  margin: auto;
  font-size: 50px;
  cursor: url(data:image/png;base64,), auto;
}

.setting .continue-button:hover {
  color: white; 
}

.setting .choices {
  text-align: left;
}

.setting .choices .prompt p {
  color: #6f8fc3;
}

.start .button,
.setting .confirm-button {
  font-size: 34px;
  padding: 5px 10px;
  height: 60px;
  cursor: url(data:image/png;base64,), auto;
  border: 4px solid transparent; 
}


/* mobile */
@media (max-width: 480px) {
  .about-page,
  .about-page button {
    font-size: 24px;
  }
  .start,
  .setting {
    line-height: 1;
    flex-direction: column;
  }

  .start .text {
    margin-left: 1vw;
    margin-right: 1vw;
  }

  .setting .story {
    width: 94vw;
  }

  .setting .play {
      width: 94vw;
  }
}

/* tablet */
@media (min-width: 481px) and (max-width: 870px) {
  .start,
  .setting {
    flex-direction: column;
  }

  .setting .story {
    width: 94vw;
  }

  .setting .play {
    width: 94vw;
}


  /* .content .text {
    margin-right: 3vw;
  } */
}

.card {
    position: relative;
    margin-right: 20px;
}

.card .card-name p {
    display: inline-block;
}

.card .front,
.card .back{
    color: lightgrey;
    padding: 3px 5px;
}

.card .front:hover,
.card .back:hover {
    color: white;
    cursor: url(data:image/png;base64,), auto;
}

.card .tooltip {
    background-color: black;
    border: 2px dashed white;
    padding: 3px;
    position: absolute;
    font-size: 25px;
    z-index: 2;
}
/* font-family: 'VT323', monospace; */
/* font-family: 'Press Start 2P', cursive; */
html {
    background-color: black;
    font-family: 'VT323', monospace;
    color: white;
}

.home-content {
    height: 90vh;
    width: 100vw;
    background-color: black;
}

.home-page {
    max-height: 100vh;
    max-width: 100vw;
    overflow-y: hidden;
}

p, button {
    color: white;
    font-family: 'VT323', monospace;
}











